import React, { useEffect } from 'react'
import { NavLink } from "react-router-dom";
import AOS from 'aos';
import HomeAbout from './HomeAbout';
import HomeHelp from './HomeHelp';
import HomeLogIn from './HomeLogIn';
import HomeNavBar from './HomeNavBar';
import HomeSteps from './HomeSteps';
import HomeThreeColumns from './HomeThreeColumns';
import HomeContact from './HomeContact';
import { useAuth } from '../contexts/AuthContext';


const Home = () => {
    useEffect(() => {
        AOS.init({duration: 2000});
    }, []);

    const { currentUser } = useAuth()

    const Info = () => (
        <div className='home__info__content__text'>
            <h1>Zacznij pomagać! Oddaj niechciane rzeczy w zaufane ręce</h1>
                <div className='home__info__buttons' data-aos="fade">
                    <NavLink to='/login' className='btn from-center'>ODDAJ RZECZY</NavLink>
                    <NavLink to='/login' className='btn from-center'>ZORGANIZUJ ZBIÓRKĘ</NavLink>
                </div>
        </div>
    )

    const UserInfo = () => (
        <div className='home__info__content__user'>
            <h1>Oddaj rzeczy, których już nie chcesz POTRZEBUJĄCYM</h1>
            <h2>Wystarczą 4 proste kroki:</h2>
            <div className='home__info__content__user__steps'>
                <div className='home__info__content__user__steps__columns'>
                    <h1>1</h1>
                    <span>Wybierz rzeczy</span>
                </div>
                <div className='home__info__content__user__steps__columns'>
                    <h1>2</h1>
                    <span>Spakuj je w worki </span>
                </div>
                <div className='home__info__content__user__steps__columns'>
                    <h1>3</h1>
                    <span>Wybierz fundację</span>
                </div>
                <div className='home__info__content__user__steps__columns'>
                    <h1>4</h1>
                    <span>Zamów kuriera</span>
                </div>
            </div>
        </div>
    )

    return (
        <>  
            <div className='home'>
                <div className='home__info'>
                    {
                        currentUser ? <div className='home__info__background__user'/> : <div className='home__info__background'/>
                    }
                    <div className='home__info__content'>
                        <HomeLogIn />
                        <HomeNavBar />
                            {
                                currentUser ? <UserInfo /> : <Info />
                            }
                    </div>
                </div>
            </div>
            <HomeThreeColumns />
            <HomeSteps />
            <HomeAbout />
            <HomeHelp />
            <HomeContact />
        </>
    )
}

export default Home